/* eslint-disable react/prop-types */
import React, { forwardRef } from "react";
import { useLocation } from "react-router-dom";
import {
  StyledLink,
  StyledExternalLink,
  StyledLinkText,
} from "./Navbar.styles";

export const MenuItemLink = forwardRef(({ to, ...rest }, ref) => {
  const { pathname } = useLocation();
  if (to) {
    return (
      <StyledLink {...rest} ref={ref} active={to === pathname} href={to} />
    );
  }

  return <StyledExternalLink {...rest} ref={ref} />;
});

export const MenuItem = forwardRef(({ label, ...rest }, ref) => {
  if (rest.submenu) {
    return (
      <StyledLinkText className="menu--item" ref={ref}>
        {label}
      </StyledLinkText>
    );
  }

  return (
    <MenuItemLink className="menu--item" {...rest} ref={ref}>
      {label}
    </MenuItemLink>
  );
});
